import {
    Login,
    Home,
    NotFound,
    Content,
    Modules
} from '../components/';


module.exports = [{
    path: '/',
    redirect: to => {
        return 'login';
    },
    hidden: true
}, {
    path: '/login',
    component: Login,
    hidden: true
}, {
    path: '/404',
    component: Home,
    hidden: true,
    children: [{
        path: '',
        component: NotFound
    }]
}, {
    path: '/wh',
    name: '',
    icon: 'inbox',
    component: Home,
    children: [{
        hidden: true,
        path: '',
        redirect: to => {
            return 'statistics'
        }
    }, {
        path: 'statistics',
        name: '',
        icon: 'inbox',
        component: Content,
        children: [{
            hidden: true,
            path: '',
            redirect: to => {
                return 'overview'
            }
        }, {
            path: 'overview',
            name: '',
            icon: 'reorder',
            component: Modules.Wh.Statistics.Overview
        }, {
            path: 'edit',
            name: '',
            icon: 'edit',
            component: Modules.Wh.Statistics.Edit
        }]
    }, {
        path: 'article',
        name: '',
        icon: 'inbox',
        component: Content,
        children: [{
            hidden: true,
            path: '',
            redirect: to => {
                return 'list'
            }
        }, {
            path: 'list',
            name: '',
            icon: 'reorder',
            component: Modules.Wh.Article.List
        }, {
            path: 'edit',
            name: '',
            icon: 'edit',
            component: Modules.Wh.Article.Edit
        }]
    }, {
        path: 'order',
        name: '',
        icon: 'inbox',
        component: Content,
        children: [{
            hidden: true,
            path: '',
            redirect: to => {
                return 'list'
            }
        }, {
            path: 'list',
            name: '',
            icon: 'reorder',
            component: Modules.Wh.Order.List
        }, {
            path: 'edit',
            name: '',
            icon: 'edit',
            component: Modules.Wh.Order.Edit
        }]
    }, {
        path: 'orderstatis',
        name: '',
        icon: 'inbox',
        component: Content,
        children: [{
            hidden: true,
            path: '',
            redirect: to => {
                return 'bar'
            }
        }, {
            path: 'bar',
            name: '',
            icon: 'reorder',
            component: Modules.Wh.OrderStatis.Bar
        }, {
            path: 'pie',
            name: '',
            icon: 'edit',
            component: Modules.Wh.OrderStatis.Pie
        }]
    }]
},{
  path: '/function',
  name: '',
  icon: 'inbox',
  component: Home,
  children: [{
    hidden: true,
    path: '',
    redirect: to => {
      return 'open'
    }
  }, {
    path: 'open',
    name: '',
    icon: 'inbox',
    component: Content,
    children: [{
      hidden: true,
      path: '',
      redirect: to => {
        return 'echarts'
      }
    }, {
      path: 'echarts',
      name: '',
      icon: 'bar-chart',
      component: Modules.Function.Open.Echarts
    }, {
      path: 'list',
      name: '',
      icon: 'reorder',
      component: Modules.Function.Open.List
    }, {
      path: 'form',
      name: '',
      icon: 'edit',
      component: Modules.Function.Open.Form
    }, {
      path: 'vuex',
      name: 'Vuex',
      icon: 'window-restore',
      component: Modules.Function.Open.Vuex
    }, {
      path: 'test404',
      name: '404',
      icon: 'window-restore',
      component: Modules.Function.Open.Test404
    }]
  }]
}, {
    path: '/adv',
    name: '',
    icon: 'inbox',
    component: Home,
    children: [{
        hidden: true,
        path: '',
        redirect: to => {
            return 'article'
        }
    }, {
        path: 'components',
        name: '',
        icon: 'inbox',
        component: Content,
        children: [{
            hidden: true,
            path: '',
            redirect: to => {
                return 'list'
            }
        }, {
            path: 'list',
            name: '',
            icon: 'reorder',
            component: Modules.Adv.Components.List
        }, {
            path: 'edit',
            name: '',
            icon: 'edit',
            component: Modules.Adv.Components.Edit
        }]
    }, {
        path: 'article',
        name: '',
        icon: 'inbox',
        component: Content,
        children: [{
            hidden: true,
            path: '',
            redirect: to => {
                return 'list'
            }
        }, {
            path: 'list',
            name: '',
            icon: 'reorder',
            component: Modules.Adv.Article.List
        }, {
            path: 'edit',
            name: '',
            icon: 'edit',
            component: Modules.Adv.Article.Edit
        }]
    }, {
        path: 'wangeditor',
        name: 'wangeditor',
        icon: 'inbox',
        component: Content,
        children: [{
            hidden: true,
            path: '',
            redirect: to => {
                return 'one'
            }
        }, {
            path: 'one',
            name: '',
            icon: 'reorder',
            component: Modules.Adv.Wangeditor.One
        }, {
            path: 'many',
            name: '',
            icon: 'edit',
            component: Modules.Adv.Wangeditor.Many
        }]
    }]
}];



